@import './style.scss';
body{
    margin: 0;
    padding: 0;
}
#bg_parent{
    width: 100%;
    height: 100vh;
    top: 0 !important;
}


.my_name{
    font-size: 40px;
    color: white;
    >span{
        color: $blue_color;
    }
}
.i_m{
    font-size: 40px;
    color: white !important;
    >span{
        color: $blue_color;
    }
}

.border_home_{
    // margin-top: -2%;
    margin-left: 10%;
    margin-right: 10%;
    border-radius: 5px;
    // height: 20%;
    padding: 2%;
    // background: #1c1c1c;
    >h1{
        color: white;
    }
    >span{
        color: white;
    }
}

#script__{
    margin: auto;
    display: none;
    text-align: left;
    color: white;
    border: 1px;
    background-color: #202020;
    width: 50%;
    border-style: solid;
    padding: 1%;
    border-radius: 5px;
    >.btn_text__{
        text-align: right;
    }
    >.btn_text__>div{
        display: inline-block;
        margin-left: 3px;
        width: 20px;
        height: 20px;
        border-radius: 50%;
    }
    .minimize{
        background: green;
    }
    .maximize{
        background: yellow;
    }
    .exit{
        background: red;
    }
}

@media only screen and (max-width: 800px){
    #script__{
        width: 70%;
    }
}