@import './style.scss';

.myprofile_{
    position: relative;
    display: flex;
    >div{
        width: 50%;
    }
    .about_me_{
        display: inline-block;
        color: white;
        padding: 4% 2%;
        text-align: right;
        .about_me_from{
            font-weight: bold;
        }
    }
    ._slider_profile{
        padding: 1% 2%;
        ul{
            border-radius: 3px;
        }
        img{
            border-radius: 3px;
        }
    }
}

@media only screen and (max-width: 800px){
    .myprofile_{
        display: block;
        .about_me_{
            width: 100%;
            padding: 0 5%;
            padding-top: 5%;
        }
        ._slider_profile{
            padding: 5%;
            width: 100%;
        }
    }
}