@import './style.scss';

.bar_home_{
    position: sticky; 
    top: 0;
    z-index: 3;
    // position: absolute;
    width: 100%;
    padding: 1% 5%;
    background: rgba($color: #000000, $alpha: 0.9);
    // right: 10%;
    

    // padding: 1% 1% 0% 0%;
    text-align: right;
    >span{
        color: white;
        margin-right: 10px;
        cursor: pointer;
        font-size: 19px;
    }
    >span:hover{
        text-shadow: 0px 0px 10px $blue_color;
        transition: 0.3s;
    }
}

@media only screen and(max-width: 1000px){
    .bar_home_{
        padding: 1.5% %5;
    }
}

@media only screen and(max-width: 800px){
    .bar_home_{
        padding: 2% %5;
    }
}